@font-face {
    font-family: 'Exo2.0';
    src: url(/../public/assets/fonts/Exo2.0-Thin.otf) format('opentype');
}

$white: #FFFFFF;
$black: #000000;

.productList__container {
    max-width: 1280px;
    height: auto;
    background:
        linear-gradient(180deg,
            rgba(0, 0, 0, 0.5) 0%,
            rgba(0, 0, 0, 5e-05) 49.88%,
            rgba(0, 0, 0, 0.5) 100%),
        url(/../public/assets/images/Background.png);
    padding: 0 80px 67px;
    margin: 50px auto;

    .productList__title {
        text-align: center;
        padding-top: 36px;
        color: $white;
        text-shadow: 1px 2px 3px $black;

        font-family: 'Exo2.0';
        font-size: 36px;
        line-height: 44px;
    }

    .productList__list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
        gap: 70px;
        margin-top: 24px;
    }
}

@media screen and (min-width: 699px) and (max-width: 1023px) {
    .productList__container {
        padding: 0 35px 67px;
        margin: 20px auto;

        .productList__list {
            gap: 30px;
        }
    }
}

@media screen and (max-width: 700px) {
    .productList__container {
        padding: 0 30px 67px;
    }
}